.navbar {
  width: 100%;
  height: 100px;
  background: #21325e;
}
.links {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: whitesmoke;
}
.links a:hover {
  filter: brightness(110%);
  transform: scale(1.1);
  transition: 0.1s ease-in;
  color: lightyellow;
}
.navbar a {
  color: whitesmoke;
  text-decoration: none;
  margin: 20px;
  font-size: 25px;
}
.toggleButton {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.toggleButton svg {
  font-size: 50px;
}
.toggleButton button {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  color: whitesmoke;
  cursor: pointer;
}
#open {
  height: 100vh;
}
#open .links {
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 100px;
}
#open a {
  width: 100%;
  text-align: center;
}
@media only screen and (max-width: 700px) {
  .navbar a {
    width: 105px;
  }
}
@media only screen and (max-width: 650px) {
  .toggleButton {
    display: flex;
  }
  #close a {
    display: none;
  }
}
@media only screen and (min-width: 650px) {
  .toggleButton {
    display: none;
  }
  .hiddenLinks {
    display: none;
  }
  #open {
    height: 100px;
  }
  #open .links {
    flex-direction: row;
  }
}
